export const LeftArrowIcon = ({ className, width = '35px', height = '35px', onClick }) => {
  return (
    <svg
      onClick={onClick}
      className={className}
      width={width}
      height={height}
      viewBox="0 0 100 100"
    >
      <path d="M 10,50 L 60,100 L 70,90 L 30,50  L 70,10 L 60,0 Z"></path>
    </svg>
  );
};

export const RightArrowIcon = ({ className, width = '35px', height = '35px', onClick }) => {
  return (
    <svg
      onClick={onClick}
      className={className}
      width={width}
      height={height}
      viewBox="0 0 100 100"
    >
      <path
        d="M 10,50 L 60,100 L 70,90 L 30,50  L 70,10 L 60,0 Z"
        transform="translate(100, 100) rotate(180) "
      ></path>
    </svg>
  );
};

